    margin: 0 0 34px 0;
  }
}

.booking__title h2 {
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 40px;
  line-height: 150%;
  color: #011C40;
  margin: 0 0 30px 0;
}
@media (max-width: 1023px) {
  .booking__title h2 {
    font-size: 34px;
    margin: 0 0 4px 0;
  }
}
@media (max-width: 767px) {
  .booking__title h2 {
    font-size: 24px;
    line-height: 130%;
    text-align: center;
    max-width: 194px;
    margin: 0 0 12px 0;
  }
}

.booking__title p {
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 150%;
  color: #011C40;
  margin: 0;
  max-width: 352px;
}
@media (max-width: 1023px) {
  .booking__title p {
    text-align: center;
    margin: 0 auto;
  }
}
@media (max-width: 767px) {
  .booking__title p {
    font-size: 16px;
    line-height: 140%;
    max-width: 257px;
  }
}

.booking__form {
  width: 403px;
  height: 455px;
  align-self: center;
  background: #011C40;
  box-shadow: 0px 4px 45px rgba(1, 28, 64, 0.15);
  padding: 50px;
}
@media (max-width: 1023px) {
  .booking__form {
    width: 100%;
    min-width: 403px;
    justify-self: center;
    padding: 44px 60px 57px 56px;
    display: grid;
  }
}
@media (max-width: 767px) {
  .booking__form {
    width: 100%;
    min-width: 290px;
    padding: 34px 30px 32px 30px;
  }
}

@media (max-width: 1023px) {
  .booking__form form {
    width: 100%;
  }
}

.booking__form h2 {
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 150%;
  color: #F9FBFD;
  margin: 0 0 35px 0;
}
@media (max-width: 1023px) {
  .booking__form h2 {
    margin: 0 0 27px 0;
  }
}
@media (max-width: 767px) {
  .booking__form h2 {
    line-height: 140%;
    text-align: center;
  }
}

.booking__input input {
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 150%;
  width: 303px;
  height: 43px;
  border: 0;
  padding: 0 0 0 16px;
}
@media (max-width: 1023px) {
  .booking__input input {
    min-width: 303px;
    width: 100%;
  }
}
@media (max-width: 767px) {
  .booking__input input {
    min-width: 230px;
  }
}
.booking__input input::placeholder {
  color: rgba(1, 28, 64, 0.7);
}
.booking__input input:hover, .booking__input input:focus {
  outline: 1px solid #add8e6;
}

.booking__input {
  margin: 0 0 20px 0;
}

.booking__input--email {
  margin: 0 0 25px 0;
}
@media (max-width: 1023px) {
  .booking__input--email {
    margin: 0 0 16px 0;
  }
}

.booking__input--checkbox {
  margin: 0 0 30px 0;
  position: relative;
}
@media (max-width: 1023px) {
  .booking__input--checkbox {
    margin: 0 0 28px 0;
  }
}

.booking__input--checkbox label {
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 400;
  font-size: 10px;
  line-height: 130%;
  color: #F9FBFD;
  padding: 0 0 0 23px;
}
@media (max-width: 767px) {
  .booking__input--checkbox label {
    display: block;
    max-width: 177px;
    padding: 0 0 0 29px;
  }
}

.booking__input--checkbox input {
  opacity: 0;
  width: 18px;
  height: 18px;
  position: absolute;
  z-index: 2;
  top: 5px;
  cursor: pointer;
}

.booking__input--checkbox input:checked ~ .booking__checkbox::before {
  position: absolute;
  content: "";
  top: 6px;
  right: 4px;
  width: 10px;
  height: 6px;
  background-image: url("../img/svg/form-vector.svg");
  background-repeat: no-repeat;
  background-size: cover;
}

.booking__input--checkbox input:focus ~ .booking__checkbox {
  outline: 1px solid #add8e6;
}

.booking__checkbox {
  position: absolute;
  width: 18px;
  height: 18px;
  left: 0;
  top: 5px;
  background: #F9FBFD;
  cursor: pointer;
}

.booking__button button {
  font-family: "Montserrat", "Arial", sans-serif;
  width: 303px;
  height: 43px;
  border: 0;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 150%;
  cursor: pointer;
}
@media (max-width: 1023px) {
  .booking__button button {
    min-width: 303px;
    width: 100%;
  }
}
@media (max-width: 767px) {
  .booking__button button {
    min-width: 230px;
  }
}
.booking__button button:hover, .booking__button button:focus {
  background: #add8e6;
  outline: 0;
}

